<template>
    <!--begin::Modal-->  
    <div class="modal fade" id="formModal5" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <form @submit.prevent="selectProduct" class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="formModal5Label" >بحث مخزن</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close">
                    </button>
                </div>
                <div class="modal-body row">
                    <div class="col-6 mt-3">
                        <label :class="`font-${$i18n.locale}`">الفئة</label>
                        <v-select v-if="categories" dir="rtl" label="name" class="vselect" style="width: 100%;"
                                    :options="categories" v-model="selected[0]" :reduce="val => val.id" @input="selectCategoryId"></v-select> 
                    </div> 
                    <div class="col-6 mt-3">
                        <label :class="`font-${$i18n.locale}`">المنتج</label>
                        <v-select v-if="products" dir="rtl" label="name" class="vselect" style="width: 100%;"
                                    :options="products" v-model="selected[1]" :reduce="val => val.id" @input="selectProductId"></v-select> 
                    </div> 
                    
                </div> 
                <div class="modal-footer">
                    <button type="button" class="btn btn-light" data-bs-dismiss="modal" >إغلاق</button>
                    <button v-if="product" type="submit" class="btn btn-primary" >حفظ</button>
                </div>
            </form>
        </div>
    </div>
    <!--end::Modal-->
</template>

<script>
import axios from 'axios';
import "vue-select/dist/vue-select.css";

export default {
    props: ['select'],
    data() {
        return {
            selected:[],
            categories: [],
            products: [],
            product: null,
        };
    }, 
    methods: {
        getParents(){
            axios.get('/api/categories',{params: {paginate:0}}).then(({data}) => {
                let that=this
                that.categories=[];
                that.products=[],
                that.product=null,
                data.success.categories.forEach(function (category) {
                    let name = that.appendParentName(category);
                    that.categories.push({id:category.id,name:name})
                }); 
            }).catch((error) => {console.log("Error......")})
        },
        selectCategoryId(category_id){
            axios.get('/api/products',{params: {paginate:0,search:{category_id:category_id}}}).then(({data}) => {
                this.products=data.success.products;
            }).catch((error) => {console.log("Error......")})
        },
        selectProductId(product_id){
            axios.get('/api/products/'+product_id).then(({data}) => {
                this.product=data.success.product;
            }).catch((error) => {console.log("Error......")})
        },
        search(){
            this.categories=[],
            this.products=[],
            this.selected=[],
            this.product=null,
            this.getParents()
            $('#formModal5').modal('show');
        },
        selectProduct(){ 
            // console.log(this.product);
            this.select(this.product)
            $('#formModal5').modal('hide');
        },  
    },
}
</script>
