<template>
  <layout-default>
    <div class="page-content">
        <div class="container-fluid">

            <!-- start page title -->
            <div class="row">
                <div class="col-12">
                    <div class="page-title-box d-flex align-items-center justify-content-between">
                        <h4 class="mb-0">تفاصيل الفاتورة</h4>
                        <form-modal ref="FormModal" :fetchData="fetchData" :addRow="addRow"/>
                        <form-options ref="FormOptions" :saveOptions="saveOptions"/>
                        <search-form ref="SearchForm" :select="addProduct"/>
                    </div>
                </div>
            </div>
          
            <!-- end page title -->
            <div class="row">
                <div class="col-lg-12" v-if="suppliers">
                    <div class="card">
                        <div class="card-body" style="margin: 20px;min-height: 600px;">
                            <div class="invoice-title row" dir="rtl">
                                <div class="col-md-4">
                                    <h4 class="font-size-16" style="text-align:center;">
                                        تاريخ الفاتورة  
                                        <div class="mt-4">
                                            <Datepicker  input-class="form-control" v-model="form.created_at" format="dd-MM-yyyy"></Datepicker>
                                        </div>
                                    </h4>
                                </div>
                                <div class="col-md-4" v-if="suppliers">
                                    <h4 class="font-size-16" style="text-align:center;">
                                        أسم المورد  
                                        <div class="mt-4">
                                            <v-select dir="rtl" label="name" class="vselect" style="width: 100%;"
                                            :options="suppliers" :reduce="val => val.id" v-model="form.supplier_id"></v-select> 
                                            
                                        </div>
                                    </h4>
                                </div>
                                <div class="col-md-4" v-if="stores">
                                    <h4 class="font-size-16" style="text-align:center;">
                                        أسم المخزن  
                                        <div class="mt-4">
                                            <v-select dir="rtl" label="name" class="vselect" style="width: 100%;"
                                            :options="stores" :reduce="val => val.id" v-model="form.store_id"></v-select> 
                                            
                                        </div>
                                    </h4>
                                </div>
                                <div class="col-md-4" v-if="store">
                                    <h4 class="font-size-16" style="text-align:center;">
                                        أسم المخزن  
                                        <div class="mt-4">
                                            <input :value="store.name" type="text" class="form-control" disabled>
                                        </div>
                                    </h4>
                                </div>
                                
                                <div class="text-muted" v-if="false">
                                    <p class="mb-1">
                                        <b style="float: left;"> أسم المورد : </b>&nbsp;{{order.supplier.name}} 
                                    </p>
                                    <p class="mb-1" v-for="(info, index) in order.supplier.info">
                                        <b style="float: left;"> {{info.name}} : </b>&nbsp;{{info.pivot.data}} 
                                    </p>
                                </div>
                            </div>

                            <hr class="my-4">

                            <div class="py-2">
                            <div class="row mb-4">

                                <h5 class="font-size-15 col-md-10">تفاصيل الفاتورة</h5>
                                <div class="col-md-1">
                                    <a href="javascript:;" @click="$refs.SearchForm.search()" class="row btn btn-success waves-effect waves-light float-end"><i class="fas fa-search-plus"></i></a>
                                </div>
                                <div class="col-md-1">
                                    <a href="javascript:;" @click="$refs.FormModal.create()" class="row btn btn-success waves-effect waves-light float-end"><i class="fas fa-plus"></i></a>
                                </div>
                            </div>
                                <div class="table-responsive" v-if="products.length==0">
                                    <table class="table table-nowrap table-centered mb-0">
                                        <tbody>
                                            <tr>
                                                <td class="col-5">
                                                    <p class="text-center">لا يوجد منتجات</p> 
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                                <div v-for="(product, index) in products">
                                    <div class="table-responsive" style="overflow: visible;">
                                        <table class="table table-nowrap table-centered mb-0">
                                            <thead>
                                                <tr>
                                                    <th>تفاصيل المنتج</th>
                                                    <th>سعر (ش)</th>
                                                    <th>سعر (ب)</th>
                                                    <th>الكمية</th>
                                                    <th>الأجمالي</th>
                                                    <th class="text-end" style="width: 120px;">خيارات</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <td class="col-5">
                                                        <label>{{product.name}} / {{appendParentName(product.category)}}</label> 
                                                        <!--
                                                            
                                                        <p class="m-1" v-if="form.order_products[index].product_id">
                                                            <a href="javascript:;" @click="$refs.FormOptions.create(index)"><i class="fas fa-pen"></i></a> 
                                                            <b>Options: </b>
                                                            <span v-for="(option, index2) in order_product.options">
                                                                {{option.option_name}} : {{option.value_name}} {{(index2+1!=order_product.options.length)?',':''}}
                                                            </span>
                                                            <span v-if="!order_product.options">
                                                                None...
                                                            </span>
                                                            <a href="javascript:;" v-else @click="order_product.options=null"><i class="far fa-times-circle text-danger"></i></a> 
                                                        </p>
                                                        -->
                                                    </td>
                                                    <td class="col-2">
                                                        <input v-model="product.order_price" @input="changeQty(index)" type="number" autocomplete="off" class="form-control" style="padding: 4px;">
                                                    </td>
                                                    <td class="col-1">
                                                        {{moneyFormat(product.price,0)}}
                                                    </td>
                                                    <td class="col-2">
                                                        <input v-model="product.qty" @input="changeQty(index)" type="number" autocomplete="off" class="form-control" style="padding: 4px;">
                                                    </td>
                                                    <td class="col-1 p-0">
                                                        {{moneyFormat(product.total,0)}}
                                                    </td>
                                                    <td class="text-end col-1">
                                                        <!--
                                                            
                                                        <a href="javascript:;" @click="removeArow(index)" class="btn btn-danger btn-sm"><i class="far fa-trash-alt"></i></a>
                                                        <a href="javascript:;" @click="$refs.FormModal.edit(product)" class="btn btn-warning btn-sm"><i class="fas fa-pen"></i></a>
                                                        <a href="javascript:;" @click="$refs.FormOptions.create(index,product.options)" class="btn btn-warning btn-sm"><i class="fas fa-pen"></i></a>
                                                        <a href="javascript:;" @click="removeOptions(index)" class="btn btn-warning btn-sm"><i class="fas fa-pen"></i></a>
                                                        -->
                                                        <div class="btn-group">
                                                            <button type="button" class="btn btn-light dropdown-toggle" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                                            المزيد <i class="mdi mdi-chevron-down"></i>
                                                            </button>
                                                            <div class="dropdown-menu" style="min-width: 120px;">
                                                                <a @click="$refs.FormModal.edit(product)" class="dropdown-item dropdownItem" href="javascript:;">
                                                                    <i class="fas fa-pen"></i> تعديل المنتج
                                                                </a>
                                                                <a @click="$refs.FormOptions.create(index,product.options)" class="dropdown-item dropdownItem" href="javascript:;">
                                                                    <i class="fas fa-pen"></i> تعديل التفاصيل
                                                                </a>
                                                                <a @click="removeOptions(index)" class="dropdown-item dropdownItem" href="javascript:;">
                                                                    <i class="far fa-trash-alt"></i> حذف التفاصيل
                                                                </a>
                                                                <a @click="removeArow(index)" class="dropdown-item dropdownItem" href="javascript:;">
                                                                    <i class="far fa-trash-alt"></i> حذف المنتج
                                                                </a>
                                                            </div>
                                                        </div>
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                    <div class="table-responsive mb-2" style="border-top:0px;" v-if="product.hasOptions">
                                        <table class="table table-nowrap table-centered mb-0" style="background-color: #ced4da73;">
                                            <thead>
                                                <tr>
                                                    <th class="text-center">بيانات المنتج</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <td class="col">
                                                        <div class="row">
                                                        <!--
                                                            v-if="option.value||option.value_text"
                                                        -->
                                                            <div class="col-md-4" v-for="option in product.options" v-if="option.value||option.value_text">
                                                                <label><b>{{option.option_name}}</b> /{{(option.value)?option.value:option.value_text}}</label> 
                                                            </div>
                                                        </div>
                                                        <!--
                                                            
                                                        <p class="m-1" v-if="form.order_products[index].product_id">
                                                            <a href="javascript:;" @click="$refs.FormOptions.create(index)"><i class="fas fa-pen"></i></a> 
                                                            <b>Options: </b>
                                                            <span v-for="(option, index2) in order_product.options">
                                                                {{option.option_name}} : {{option.value_name}} {{(index2+1!=order_product.options.length)?',':''}}
                                                            </span>
                                                            <span v-if="!order_product.options">
                                                                None...
                                                            </span>
                                                            <a href="javascript:;" v-else @click="order_product.options=null"><i class="far fa-times-circle text-danger"></i></a> 
                                                        </p>
                                                        -->
                                                    </td>
                                                    
                                                </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                                <div class="d-print-none mt-4">
                                    <div class="float-end">
                                        <th scope="row" colspan="6" class="border-0 text-end">إجمالي الفاتورة : </th>
                                        <td class="border-0 text-end"><h4 class="m-0"> {{moneyFormat(total,0)}} </h4></td>
                                    </div>
                                    <div class="float-start" v-if="products.length">
                                        <a href="javascript:;" @click="saveOrder()" class="btn btn-success w-md waves-effect waves-light">حفظ</a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <!-- end row -->
            
        </div> <!-- container-fluid -->
    </div>
  </layout-default>
</template>  


<script>
import LayoutDefault from '../../layouts/App';
import FormModal from '../../components/modals/product/add';
import FormOptions from '../../components/modals/supplier/options';
import SearchForm from '../../components/modals/product/search';
import PaginationNav from '../../components/pagination';
import axios from 'axios';
import {Form,HasError} from 'vform'
import Datepicker from 'vuejs-datepicker';
import Vue from "vue";

export default {
  components: {LayoutDefault,PaginationNav,HasError,Datepicker,FormModal,SearchForm,FormOptions},
  data() {
        return {
            form: new Form({id:null,supplier_id:null,store_id:null,created_at:new Date().toLocaleString(),order_products:[]}),
            products:[],
            stores:null,
            store:null,
            suppliers:null,
            options:null,
            total:0,
        }
    },
    watch: {
        products: {
            handler: function(form) {
                let total=0;
                form.forEach(function (product) {
                    total+=parseInt(product.total) 
                    let hasOptions=false
                    product.options.forEach(function (option) {
                        if(option.value_id||option.value_text){hasOptions=true}
                    })
                    product.hasOptions=hasOptions
                })
                this.total=total
            },
            deep: true
        },
    },  
  created() { 
    this.fetchData();
  },
  methods: {
    changeQty(index) {
        // console.log('222');
        const newValue = { ...this.products[index]};
        newValue.total=newValue.qty*newValue.order_price

        Vue.set(this.products, index, newValue);
    },
    addProduct(product) {
        product.qty=1
        product.order_price=parseInt(product.price) 
        product.total=product.price
        product.hasOptions=false
        product.options=[]
        this.options.forEach(function (option) {
            product.options.push({
                option_id:option.id,
                option_name:option.name,
                option_type:option.input_type,
                option_details:option.details,
                value_id:null,
                value:null,
                value_text:null,
            });
        });
        this.products.push(product);
        // console.log(product);
    },
    removeArow(index) {
        this.products.splice(index, 1);
    },
    saveOptions(index,options) {
        const newValue = { ...this.products[index], options: options };
        Vue.set(this.products, index, newValue);
    },
    removeOptions(index) {
        this.products[index].options.forEach(function (option) {
            option.value_id=null
            option.value=null
            option.value_text=null
        });
        // const newValue = { ...this.products[index], options: this.products[index].options };
        // Vue.set(this.products, index, newValue);
    },
    addRow(product_id) {
        axios.get('/api/products/'+product_id).then(({data}) => {
            let that=this
            this.products.forEach(function (product,index) {
                if(product.id==data.success.product.id){
                    that.products[index].price=data.success.product.price
                    that.products[index].order_price=data.success.product.price
                    that.products[index].name=data.success.product.name
                }
            });
        }).catch((error) => {console.log("Error......")})
    },
    async fetchData() {
        await axios.get('/api/supplier_orders/create').then(({data}) => {
            this.suppliers=data.success.suppliers;
            this.options=data.success.options;
            if(data.success.stores){
                this.stores=data.success.stores;
            }else{
                this.form.store_id=data.success.store.id
                this.store=data.success.store
            }
        });
    },
    saveOrder() {
      this.form.order_products=this.products
      this.form.post('/api/supplier_orders').then(({data}) => {
        //   console.log(data)
        this.$router.push({ name: "supplier_orders_show",params:{order_id:data.success}})
      })
    },
  },
}
</script>

<style scoped>
    input[type="number"]::-webkit-outer-spin-button,
    input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
    }
    .table-responsive {
        border: 3px solid #ced4da;
        border-radius: 5px;
        /*height: 500px; */
    }
    /* width */
    .table-responsive::-webkit-scrollbar {
    width: 10px;
    }

    /* Track */
    .table-responsive::-webkit-scrollbar-track {
    background: #f1f1f1; 
    }
    
    /* Handle */
    .table-responsive::-webkit-scrollbar-thumb {
    background: #888; 
    }

    /* Handle on hover */
    .table-responsive::-webkit-scrollbar-thumb:hover {
    background: #555; 
    }
</style>