<template>
    <!--begin::Modal-->  
    <div class="modal fade" id="formOption" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <form @submit.prevent="store" class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="formOptionLabel" >تعديل بيانات المنتج</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close">
                    </button>
                </div>
                <div class="modal-body row">
                    <div class="col-6 mt-3" v-for="(option, index) in form.options">
                        <label> {{option.option_name}}</label>
                        <v-select v-if="option.option_type==1" label="name" class="vselect" style="width: 100%;"
                            :options="option.option_details" :reduce="val => val.id" v-model="form.options[index].value_id"></v-select> 
                        <input v-else v-model="form.options[index].value_text" type="text" autocomplete="off" class="form-control">
                    </div> 
                </div> 
                <div class="modal-footer">
                    <button type="button" class="btn btn-light" data-bs-dismiss="modal" >Close</button>
                    <button type="submit" class="btn btn-primary" >Save</button>
                </div>
            </form>
        </div>
    </div>
    <!--end::Modal-->
</template>

<script>
import {Form,HasError} from 'vform'
import axios from 'axios';
import "vue-select/dist/vue-select.css";


export default {
    components: {HasError},
    props: ['saveOptions'],
    data() {
        return {
            form: new Form({index:null,options:[]}),
        };
    }, 
    methods: {
        create(index,options){
            console.log(options);
            this.form.reset();
            this.form.index=index;
            this.form.options=options;
            $('#formOption').modal('show');
        },
        store(){ 
            $('#formOption').modal('hide');
            let options=[]
            let that=this
            this.form.options.forEach(function (option,i) {
                if(option.value_text==""){that.form.options[i].value_text=null}
                if(option.value_id){
                    option.option_details.forEach(function (value) {
                        if(value.id==option.value_id){
                            that.form.options[i].value=value.name;
                        }
                    })
                }else{
                    that.form.options[i].value=null;
                }
                // options.push(option)
            })
            // console.log(this.form.options)
            this.saveOptions(this.form.index,this.form.options)
        },  
    },
}
</script>
